import { Person } from './person';
import { Hero } from './characters-module/hero';
import { Meet } from './characters-module/meet';
import { CharacterFactory } from './core/character-factory';
import { SpeechOutputStrategy } from './core/strategy/speech-output-strategy';
class Main {
    run() {
        const jl = new Person();
        const kevin = new Person();
        jl.forname = 'Jean-Luc';
        kevin.forname = 'Kevin';
        jl.setBirthDate(new Date('1968-04-30'));
        kevin.setBirthDate(new Date('1981-12-06'));
        const batman = new Person();
        batman.forname = 'Batman';
        console.log('Bonjour ' + jl.forname + '(' + jl.getAge() + ')');
        console.log('Salut ' + kevin.forname + '(' + kevin.getAge() + ')');
        console.log('Hola ' + batman.forname + '(' + batman.getAge() + ')');
    }
    game() {
        const superman = CharacterFactory.createCharacter('superman');
        CharacterFactory.type = 'Bad';
        const lexLuthor = CharacterFactory.createWithStrength('Lex Luthor', 100);
        const joker = CharacterFactory.createFullCharacter('Joker', 100, 200);
        lexLuthor
            .setLifePoints(150);
        const batman = new Hero('Batman');
        // Initiate a meet
        const meet = new Meet(superman, lexLuthor, new SpeechOutputStrategy());
    }
}
const app = new Main();
let button = document.getElementById('fight');
button.addEventListener('click', () => {
    app.game();
});
